*{
  margin: 0;
  box-sizing: border-box;
}
body{
  background-color: #f4f4f4;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  text-align: center;
  height: 100vh;
}
img{
  width: 220px;
}
.equal{
  font-weight: bold;
  font-size: 40px;
}

.currency{
  padding: 40px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.currency select{
  padding: 10px 20px 10px 10px;
  border: 1px solid #dedede;
  font-size: 16px;
  background-color: transparent;
}

.currency input{
  text-align: right;
  font-size: 30px;
  border: 0;
  background-color: transparent;
}
.currency input:focus,select:focus{
  outline: none;
}